.ts_error_page {
    position: relative;
    height: calc(100vh - 330px);
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15px;
    height: 100%;
}

.ts_error_page_bg {
    background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    background-color: var(--bs-body-bg);
    width: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index: 0;
    opacity: .5;
}

.ts_error_page_wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 700px;
    min-height: 230px;
    position: relative;
    z-index: 1;
    background-color: var(--bs-body-bg);
    padding: 30px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.06), 0 2px 4px rgba(0, 0, 0, 0.04), 0 5px 14px rgba(0, 0, 0, 0.02);
    border-radius: 4px;
}

.ts_error_title {
    font-size: calc(.5vh + .5vw + 12px);
    text-align: center;
    margin-bottom: 12px;
    font-weight: 600;
    color: var(--bs-body-color);
    font-family:Inter, sans-serif;
}

.ts_error_des {
    text-align: center;
    display: block;
    font-size: 14px;
    margin-bottom: 15px;
    color: var(--bs-body-color);
    font-weight: 400;
    font-family:Inter, sans-serif;
}

.ts_error_page {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    line-height: 1.5;
    min-height: 100%;
    padding: 30px 15px;
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    background: var(--bs-body-bg);
    z-index: 1;
}
.ts_error_page_actions {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.ts_error_action_list {
    padding: 10px;
}

.ts_error_btn_primary, .ts_error_btn_secondary{
    display: inline-block;
    font-weight: 400;
    color: #2b2b2b;
    text-align: center;
    vertical-align: middle;
    padding: 0.5rem 1rem;
    font-size: 16px;
    line-height: 1.5;
    border: 1px solid transparent;
    border-radius: 3px;
    cursor: pointer;
    min-width: 140px;
    transition: color .15s ease,background-color .15s ease,border-color .15s ease,box-shadow .15s ease,-webkit-box-shadow .15s ease;
    margin: 0 10px;
}

.ts_error_des .manage-feed {
    color: #ff4051;
}

.ts_error_des .strongCssSpan {
    color: #c20e5e;
    font-weight: 600;
    text-transform: capitalize;
    display: inline;
}
.ts_error_btn_primary{
    color: #fff;
    background-color: #5354fc;
    border-color: #5354fc;
    font-family: Inter, sans-serif;
}

.ts_error_btn_secondary{
    color: #2b2b2b;
    background-color: transparent;
    border-color: #2b2b2b;
}

.ts_error_btn_primary:hover, .ts_error_btn_primary:focus{
    color: #fff;
    background-color: #4025f9;
    border-color: #4025f9;
}
.ts_error_btn_secondary:hover, .ts_error_btn_secondary:focus{
    color: #fff;
    background-color: #2b2b2b;
    border-color: #2b2b2b;
}

.ts_error_page_logo_ico {
    background-repeat: no-repeat;
    background-position: right top;
    height: 50px;
    background-size: contain;
    width: 150px;
}

.ts_error_page_logo {
    position: absolute;
    bottom: -75px;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}