body .website__ .uppy-Dashboard-inner {
       max-width: 100%;
}

body .website__ .uppy-Dashboard-browse:before {
       display: none;
}

body .website__ .uppy-Dashboard-AddFiles-title {
       padding: 0 10px;
       font-size: 12px;
}

.icon-bg-gradient {
       background-image: linear-gradient(to right top, #4e69ed, #6264ef, #755eef, #8757ef, #984eed);
}

[data-product="taggbox"] .icon-bg-gradient {
       background-image: linear-gradient(to right top, #ff4051, #ff4051, #ff4051, #ff4051, #ff4051);
}

.embed_size_ .selectpicker__control {
       border: none !important;
       border-top-right-radius: 3px;
       border-bottom-right-radius: 3px;
}

.tag_iframe_preview {
       height: 500px;
}

.tag_iframe_preview iframe {
       border: none;
       transform: scale(0.65);
       width: 153%;
       margin-left: -26.5%;
       height: 750px;
       margin-top: -130px;
       margin-bottom: -130px;
}

.tagshop {
       position: relative;
       z-index: 1;
}

/*-- Publish-START --*/

.publish-content {
       .full-content {
              padding-top: 50px;
              padding-bottom: 0;

              .container-fluid {
                     padding-left: 0;
                     padding-right: 0;

                     .content_inner {
                            border-radius: 0;

                            &.card {
                                   background-color: transparent;
                            }

                            .a_header_ {
                                   height: 100%;

                                   .platform-selection {

                                          label,
                                          span,
                                          button {
                                                 width: 48%;
                                                 height: 132px;
                                                 display: flex;
                                                 align-items: center;
                                                 justify-content: center;
                                          }
                                   }

                            }

                            .preview-container {
                                   background-color: var(--bs-body-bg);
                                   border-radius: 3px;
                                   box-shadow: 0 5px 20px 0 #20303c1c;
                                   padding: .8em;
                                   margin: 0 auto;
                                   height: calc(100vh - 197px);
                                   max-height: 730px;
                                   transition: all .5s ease;

                                   &.mobile-view {
                                          max-width: 375px;
                                          min-height: 430px;
                                   }

                                   .theme_preview {
                                          transition: all .5s ease;
                                          iframe {
                                                 transition: all .5s ease;
                                          }
                                   }
                            }

                            .desktop-mob {
                                   margin-bottom: 10px;

                                   .btn {
                                          padding-top: 6px;
                                          padding-bottom: 6px;

                                          &.btn-dark {
                                                 background-color: var(--bs-dark);
                                          }

                                          &.btn-outline-dark {

                                                 &:hover {
                                                        color: var(--bs-dark);
                                                        background-color: var(--bs-gray-200);
                                                 }
                                          }

                                          &+.btn {
                                                 border-left: 0;
                                          }
                                   }
                            }

                            .custom-preview {
                                   background-color: var(--bs-gray-1000);
                            }

                            .custom-schema-main {
                                   --bs-card-spacer-y: 1rem;
                                   --bs-card-spacer-x: 1rem;
                                   --bs-card-title-spacer-y: 0.5rem;
                                   --bs-card-border-width: 1px;
                                   --bs-card-border-radius: .42rem;
                                   --bs-card-box-shadow: var(--bs-box-shadow);
                                   --bs-card-inner-border-radius: .42rem;
                                   --bs-card-cap-padding-y: 0.5rem;
                                   --bs-card-cap-padding-x: 1rem;
                                   --bs-card-cap-bg: transparant;
                                   --bs-card-cap-color: ;
                                   --bs-card-height: ;
                                   --bs-card-color: var(--bs-gray-700);
                                   --bs-card-img-overlay-padding: 1rem;
                                   --bs-card-group-margin: 0.75rem;
                                   --bs-card-bg: var(--bs-tertiary-bg);
                                   height: var(--bs-card-height);
                                   background-color: var(--bs-body-bg);
                                   border: var(--bs-card-border-width) solid var(--bs-body-bg);
                                   border-radius: var(--bs-card-border-radius);
                                   box-shadow: var(--bs-card-box-shadow);
                                   margin: 20px;

                                   .card-header {
                                          border: 0;

                                          .nav-link {
                                                 &.btn {
                                                        --bs-btn-padding-x: 1rem;
                                                        --bs-btn-padding-y: 0.5rem;
                                                        padding-top: 6px;
                                                        padding-bottom: 6px;
                                                        border: 1px solid var(--bs-gray-800);
                                                        color: var(--bs-gray-800);


                                                        &.active,
                                                        &:hover {
                                                               color: var(--bs-white);
                                                               background-color: var(--bs-gray-800);
                                                        }
                                                 }
                                          }
                                   }
                            }
                     }

              }
       }

       .custom-card {
              .aside__body {
                     .tab-content {
                            .nav-item {
                                   .nav-link {

                                          &:focus,
                                          &:hover {
                                                 color: var(--bs-dark);
                                          }

                                          &.active {
                                                 // color: var(--bs-btn-active-color);
                                                 // background-color: var(--bs-btn-active-bg);
                                                 // border-color: var(--bs-btn-active-border-color);
                                                 color: var(--bs-light);
                                                 // background-color: var(--bs-black);
                                                 background-color: var(--bs-primary);
                                                 // border-color: var(--bs-dark);

                                                 &:hover {
                                                        color: var(--bs-light);
                                                        cursor: default;
                                                 }
                                          }
                                   }
                            }
                     }
              }

              .select-grid {

                     .btn-check:checked+.btn,
                     :not(.btn-check)+.btn:active,
                     .btn:first-child:active,
                     .btn.active,
                     .btn.show,
                     .btn:focus,
                     .btn:hover {
                            background-color: var(--bs-black);
                            color: var(--bs-white);
                     }
              }
       }

       .rating-schema-step {
              ul {
                     li {
                            position: relative;
                            padding-left: 15px;


                            &:before {
                                   content: '•';
                                   position: absolute;
                                   top: 9px;
                                   transform: translateY(-50%);
                                   color: var(--bs-gray-600);
                                   left: 0;
                            }
                     }

              }
       }

       .rating-gallery {
              .card-body {
                     height: calc(100vh - 100px);
              }
       }
}

[data-theme="dark"] {
       .publish-content {
              .custom-card {
                     .aside__body {
                            .tab-content {
                                   .nav-item {
                                          .nav-link {
                                                 &.active {
                                                        color: var(--bs-white);
                                                 }
                                          }
                                   }
                            }
                     }
              }

              .full-content {
                     .container-fluid {
                            .content_inner {
                                   .desktop-mob {
                                          .btn.btn-dark {
                                                 color: var(--bs-white);
                                                 background-color: var(--bs-black);
                                                 border: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color) !important;
                                          }

                                          .btn-outline-dark {
                                                 color: var(--bs-nav-link-color);
                                          }
                                   }
                            }
                     }
              }
       }
}